.header {
  margin: 0.9375rem 0.625rem;
}
.header__descript {
  text-align: center;
}
.header__descript h1 {
  font-size: 1.25rem;
}
.main .card {
  margin: 0.625rem;
}
.main .card__about {
  margin-bottom: 0.9375rem;
}
.main .card__about h2 {
  margin-bottom: 0.9375rem;
}
.main .card__projects {
  text-align: center;
}
.main .card__projects h3 {
  text-align: start;
  margin-bottom: 0.9375rem;
}
.main .card__projects--webDev {
  position: relative;
  display: inline-block;
}
.main .card__projects--webDev .dropdownOne {
  background-color: #3498DB;
  color: white;
  padding: 1rem;
  font-size: 1.125rem;
  border: none;
  cursor: pointer;
}
.main .card__projects--webDev .content {
  display: none;
  position: absolute;
  background-color: #f1f1f1;
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 1;
}
.main .card__projects--webDev .content a {
  color: black;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
}
.main .card__projects--gameDev {
  position: relative;
  display: inline-block;
}
.main .card__projects--gameDev .dropdownTwo {
  background-color: #3498DB;
  color: white;
  padding: 1rem;
  font-size: 1.125rem;
  border: none;
  cursor: pointer;
}
.main .card__projects--gameDev .content {
  display: none;
  position: absolute;
  background-color: #f1f1f1;
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 1;
}
.main .card__projects .show {
  display: block;
}

.footer {
  margin: 0.9375rem 0.625rem;
  position: relative;
  height: 170px;
}
.footer__socials {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.footer__copy {
  position: absolute;
  bottom: 0;
  right: 0;
}
:root {
  --font-Inter: "Inter", sans-serif;
}

:root {
  /* Primary: */
  --limeGreen: hsl(163, 72%, 41%);
  --brightRed: hsl(356, 69%, 56%);
  --facebook: hsl(208, 92%, 53%);
  --twitter: hsl(203, 89%, 53%);
  --instagram-LG-start: hsl(37, 97%, 70%);
  --instagram-LG-end: hsl(329, 70%, 58%);
  --youTube: hsl(348, 97%, 39%);
  /* Dark Theme */
  --veryDarkBlue-BG: hsl(230, 17%, 14%);
  --veryDarkBlue-TopBGPattern: hsl(232, 19%, 15%);
  --darkDesaturatedBlue-CardBG: hsl(228, 28%, 20%);
  --desaturatedBlue-Text: hsl(228, 34%, 66%);
  --white-Text: hsl(0, 0%, 100%);
  --Toggle-LG-start: hsl(210, 78%, 56%);
  --Toggle-LG-end: hsl(146, 68%, 55%);
  --toggleBtn-dark: linear-gradient(225deg, var(--Toggle-LG-end) 0%, var(--Toggle-LG-start) 98.02%);
  --dark-card-hover: hsl(228, 25%, 27%);
  /* Light Theme */
  --toggle: hsl(230, 22%, 74%);
  --white-BG: hsl(0, 0%, 100%);
  --veryPaleBlue-TopBGPattern: hsl(225, 100%, 98%);
  --lightGrayishBlue-CardBG: hsl(227, 47%, 96%);
  --darkGrayishBlue-Text: hsl(228, 12%, 44%);
  --VeryDarkBlue-Text: hsl(230, 17%, 14%);
  --light-card-hover: hsl(228, 33%, 91%);
  /* Transition */
  --transition: all 150ms ease-in-out;
  --toggleBtn: var(--toggleBtn-dark);
  --bg: var(--veryDarkBlue-BG);
  --bgTop: var(--veryDarkBlue-TopBGPattern);
  --cardBg: var(--darkDesaturatedBlue-CardBG);
  --textColor: var(--desaturatedBlue-Text);
  --textColor2: var(--white-Text);
  --cardHover: var(--dark-card-hover);
}

@media (prefers-color-scheme: light) {
  :root {
    --toggleBtn: var(--toggle);
    --bg: var(--white-BG);
    --bgTop: var(--veryPaleBlue-TopBGPattern);
    --cardBg: var(--lightGrayishBlue-CardBG);
    --textColor: var(--darkGrayishBlue-Text);
    --textColor2: var(--VeryDarkBlue-Text);
    --cardHover: var(--light-card-hover);
  }
}
body.dark {
  --toggleBtn: var(--toggleBtn-dark);
  --bg: var(--veryDarkBlue-BG);
  --bgTop: var(--veryDarkBlue-TopBGPattern);
  --cardBg: var(--darkDesaturatedBlue-CardBG);
  --textColor: var(--desaturatedBlue-Text);
  --textColor2: var(--white-Text);
  --cardHover: var(--dark-card-hover);
}

body.light {
  --toggleBtn: var(--toggle);
  --bg: var(--white-BG);
  --bgTop: var(--veryPaleBlue-TopBGPattern);
  --cardBg: var(--lightGrayishBlue-CardBG);
  --textColor: var(--darkGrayishBlue-Text);
  --textColor2: var(--VeryDarkBlue-Text);
  --cardHover: var(--light-card-hover);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

body {
  background-color: var(--bg);
  color: var(--textColor);
  cursor: default;
}

